h1, h2 {
    color: #747474;
    font-family: "Anaheim";
    text-align: center;
}

.body {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
}

.container {
    padding-left: 50px;
    padding-right: 50px;
}

.homeContainer {
    background-image: url('../imgs/home-bg.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
    overflow-x: hidden;
}

.fill-window {
    height: 100%;
    position: absolute;
    left: 0;
    width: 100%;
    overflow: hidden;
}

.nameLogo {
    width: 15%;
    height: auto;
}
